.pagee {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 99vw;
    height: auto;
    background-color: rgb(47, 47, 47);
    background-repeat: no-repeat;
    background-size: cover;
}

.backToTop {
    background-color: #dae0e6;
    border: none;
    text-decoration: underline;
    color: #0079db;
    cursor: pointer;
    padding-bottom: 109px;
}

.outer-page {
    display: flex;
    background-color: #dae0e6;
    justify-content: center;
    width: 1387px;
}

html {
    background-color: #dae0e6;
}

.posts {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    box-shadow: 0px 0px 4px 3px rgba(216, 214, 214, 0.918);
    position: relative;
    border-radius: 2px;
    margin: 30px 0px 10px;
    width: 800px;
    height: auto;
    background-color: white;
    padding-bottom: 30px;
}

.newCommentEditForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: auto;
    width: 800px;
    background-color: white;
    border-radius: 2px;
    margin-top: 5px;
}

.sign-in-or-up {
    margin: 0px 4px;
    text-decoration: none;
    color: #0079db;
}

.not-signed-in-comment {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
}

.li {
    overflow-wrap: normal;
    list-style: none;
    margin-bottom: 10px;
}

.deleteEditBtn {
    display: flex;
    justify-content: flex-end;
}

.img-tag {
    display: flex;
    height: 425px;
    width: 749px;
    border-radius: 2px;
}

.btnsDiv {
    display: flex;
    justify-content: flex-end;
}

.post-title {
    display: flex;
    font-size: 14px;
    font-weight: 550;
    margin-top: 10px;
    margin-left: 5px;
    letter-spacing: .3px;
}

.username-span {
    margin-left: 3px;
    font-weight: bold;
}

.post-username {
    display: flex;
    font-size: 12px;
    font-weight: 200;
    margin-top: 10px;
    margin-left: 5px;
    letter-spacing: .3px;
}

.left-postt {
    display: flex;
    align-items: center;
    flex-direction: column;
    float: left;
    height: 465px;
    width: 37px;
    border-radius: 2px;
    border-right: 1px solid white;
    background-color: white;
}

.left-postt button {
    border: 1px solid white;
    background-color: white;
}

.left-postt button:hover {
    cursor: pointer;
}

.left-postt img {
    border-radius: 4px;
    height: 30px;
    width: 30px;
    background-color: white;
}

.one-post-main-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 99%;
    width: 744px;
    text-align: center;
}

.one-post-image {
    margin-top: 20px;
    margin-left: 5px;
    height: 425px;
    width: 767px;
    border-radius: 4px;
}

.left-postt {
    /* border: 1px solid white; */
    background-color: white;
    /* width: auto; */
}

.comment-reply-div {
    margin-left: 10px;
}

.commm {
    overflow-wrap: anywhere;
    font-weight: 300;
    margin-left: 31px;
    height: fit-content;
    padding-left: 5px;
    margin-top: 5px;
    font-size: 16px;
}

.comment .btnsDiv {
    display: flex;
}

.username-profile-pic {
    display: flex;
    flex-direction: row;
    margin-left: 21px;
    margin-bottom: 2px;
    margin-top: 5px;
}

.username-profile-pic h5 {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 5px;
}

.profile-pic {
    display: flex;
    /* border: 1px solid red; */
    border-radius: 50%;
    height: 20px;
    width: 20px;
}

.comment {
    display: flex;
    flex-direction: column;
    margin: 0px 2px;
    font-size: 12px;
    background-color: transparent;
    backdrop-filter: blur(5px);
}

.on-comment-username {
    display: flex;
    /* margin-left: 22px; */
    justify-content: center;
    text-align: center;
    font-size: 14px;
    /* margin-bottom: 5px; */
}

.error-div {
    display: flex;
    justify-content: center;
    width: 300px;
    background-color: transparent;
    overflow-wrap: break-word;
    margin: 0;
    padding-right: 20px;
    margin-top: 20px;
}

.errors {
    display: flex;
    float: left;
    justify-content: center;
    background-color: transparent;
    font-size: 12px;
    overflow-wrap: break-word;
    margin: 0px;
    margin-right: 50px;
}

.comment_on_comment {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: right;
}

.idk {
    display: flex;
}

#one-post-edit-btn-icon {
    height: 27px;
    width: 20px;
    /* margin-right: 6px; */
    margin-left: 3px;
    margin-bottom: 3px;
}

#one-post-delete-btn-icon {
    height: 23px;
    width: 22px;
    /* margin-right: 6px; */
    margin-left: 1px;
    margin-bottom: 3px;
}

.comments button {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    /* margin-top: 10px; */
    width: 73px;
    height: auto;
    /* margin-left: 2px; */
    font-size: 12px;
    text-align: center;
    padding: 5px;
    color: #626262;
    /* border: 1px solid #e4e3e3; */
}

.comments button:hover {
